@import '/src/style/variables.scss';

.search-field {
	position: relative;
	max-width: 360px;
	margin-left: auto;
	flex: 0 0 calc(100% / 3);
	justify-content: center;
	@media (max-width: 767.98px) {
		min-width: 180px;
	}
	&__input {
		display: block;
		border: 1px solid $main-color-grey-400;
		padding: 12px 16px;
		border-radius: 4px;
		width: 100%;
		color: $main-color-grey-900;
		background: url('/img/search.png') no-repeat right center/16px;
		background-origin: content-box;
		position: relative;
		&:focus,
		&:focus-visible {
			outline: 1px solid $main-color-success;
		}
		&::placeholder {
			font-family: 'Lato';
			font-weight: 400;
			font-size: 14px;
			color: $main-color-grey-600;
		}
	}
	&__results-list {
		z-index: 2;
		display: block;
		position: absolute;
		width: 100%;
		min-height: 500px;
		top: 100%;
		left: 0;
		background: #f4f5f7;
		box-shadow: 0px 0px 6px -2px $main-color-grey-900;
		border-radius: 4px;
		color: $main-color-grey-900;
		padding: 6px 0;
	}
	&__result-list-item {
		display: flex;
		gap: 12px;
		position: relative;
		background: #f4f5f7;
		padding-left: 16px;
		padding-right: 16px;

		&:not(:first-child) {
			padding-top: 6px;
		}
		&:not(:last-child) {
			padding-bottom: 6px;
			border-bottom: 1px solid #e5e8ed;
		}
	}
	&__result-img {
		width: 40px;
		height: 40px;
		object-fit: cover;
	}
	&__result-text {
		flex: 10 1 100%;
	}
	& .price-container {
		justify-content: flex-end;
	}
	&__result-string {
		background: rgba($color: $main-color-success, $alpha: 0.2);
	}
}
